import { FormStyled } from "./FormStyled";
import { useState } from "react";
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import NativeSelect from '@mui/material/NativeSelect';
import img from '../../img/w3.jpg'
const Form = ({ onSubmit }) => {
    
    const [widthStitch, setWidthStitch] = useState('');
    const [heightStitch, setHeightStitch] = useState('');
    const [marginStitch, setMarginStitch] = useState('');
    const [size, setSize] = useState('');
    const [amountYarn, setAmountYarn] = useState('')
  
    const handleChange = evt => {
        console.log(evt.target.value)
        const { name, value } = evt.currentTarget;

        switch(name) {
          case 'widthStitch':
          setWidthStitch(value);
          break;
  
          case 'heightStitch':
          setHeightStitch(value);
          break;
          
          case 'marginStitch':
          setMarginStitch(value);
          break;
  
          case 'size':
          setSize(value);
          break;

          case 'amountYarn':
          setAmountYarn(value);
          break;

          default:
          return;

          }
      };

  
    const handleSubmit = e => {
        e.preventDefault();
        onSubmit({ widthStitch, heightStitch, marginStitch, size, amountYarn });
        resetForm();
    };


  
    const resetForm = () => {
        setWidthStitch('');
        setHeightStitch('');
        setMarginStitch('');
        setSize('');
    }

    return (

        <FormStyled autocomplete="off" onSubmit={handleSubmit}>
            
            <Box sx={{display: 'flex', alignItems: 'center'}}>
            <label>   :
            <TextField 
            sx={{width: '60px', marginRight: '5px'}}
            variant="outlined" 
            type="text" 
            name="widthStitch" 
            onChange={handleChange} 
            value={widthStitch}
            size="small"
            required
            />
            x
            <TextField 
            sx={{width: '60px', marginLeft: '5px'}}
            variant="outlined" 
            type="text" 
            name="heightStitch" 
            onChange={handleChange} 
            value={heightStitch}
            size="small"
            required
            />
            </label>
            </Box>
            <Box sx={{display: 'flex', alignItems: 'center'}}>
            <label>  ():
            <TextField 
            type="text" 
            sx={{width: '60px'}}
            name="marginStitch" 
            onChange={handleChange}
            value={marginStitch}
            size="small"
            required
            />
            </label>
            </Box>
            <Box sx={{display: 'flex', alignItems: 'center'}}>
            <label>    :
            
            <NativeSelect 
            name="amountYarn" 
            value={amountYarn} 
            onChange={handleChange} 
            size="small" 
            sx={{width: '150px'}} 
            required>
                <option> ...</option>
                <option value="1">1 </option>
                <option value="2">2 </option>
            </NativeSelect>
            </label>
            </Box>

            <Box sx={{display: 'flex', alignItems: 'center'}}></Box>
            <label>   :
            <NativeSelect 
            name="size" 
            value={size} 
            onChange={handleChange} 
            size="small" 
            sx={{width: '150px'}} 
            required>
                <option> ...</option>

                <optgroup label="">
                    <option value="14">14 </option>
                    <option value="16">16 </option>
                    <option value="18">18 </option>
                    <option value="20">20 </option>
                    <option value="22">22 </option>
                </optgroup>

                <optgroup label="  ">
                    <option value="25">25 </option>
                    <option value="27">27 </option>
                    <option value="28">28 </option>
                    <option value="30">30 </option>
                    <option value="32">32 </option>
                    <option value="36">36 </option>
                    <option value="40">40 </option>
                    <option value="46">46 </option>
                    <option value="56">56 </option>
                </optgroup>

            </NativeSelect>
            </label>
       
            <Button type="submit" variant="contained" sx={{backgroundImage: `url(${img})`}}></Button>
        </FormStyled>

    )
}

export default Form;